import React, { useState } from 'react';
import {DropzoneArea} from 'material-ui-dropzone'
import { Formik, Form, Field } from 'formik';
import Alert from '@material-ui/lab/Alert';
import { TextField} from 'formik-material-ui';
import { Button,Grid , Container,Paper } from '@material-ui/core';
import { makeStyles } from "@material-ui/core/styles";
import * as yup from 'yup';
import axios from '../../axios'
const validationSchema = yup.object({
  mobileNo: yup.string()
      .required("Registered Mobile No Required")
      .min(10, "Mobile number is not valid")
      .max(10, "Mobile number is not valid"),
  formNo: yup.string()
      .required("Form  No Required")
      .min(1, "Form No is Not valid")
      .max(10, "Form No is not valid"), 
  });
  const useStyles = makeStyles((theme) =>({
    
    textbox: {
      backgroundColor: '#fff',
      transform: 'upper'
    }
  }));
  export default function PhotoUpdate() {
  const classes = useStyles();
  const [base64, setBase64]=useState('');
  const [file, setFile]=useState('');
  const [fileerror, setFileerror]=useState(true)
  const handleFileChange=(files)=>{
    if(files[0]!==undefined)
    {       
      let reader = new FileReader();
      reader.readAsDataURL(files[0]);
      reader.onloadend = () => {
      setFile(file[0])
      setBase64(reader.result)  
      setFileerror(false) 
    }
    }
  }
  return (
    <>
      <Container maxWidth='lg'>
      <Paper elevation='3'>
     <div style={{"border": '1px', "borderStyle": 'Solid', 'borderColor': '#e2e2e2', "backgroundColor": '#f2fcff', "padding": '30px'}}>
     <Formik
        initialValues={{mobileNo: '', formNo: ''}}
        validationSchema={validationSchema}
        onSubmit=  {(values, { setSubmitting, resetForm }) => {
            values.photo=base64
            setTimeout(() => {
            setSubmitting(false);       
            axios.post('api/admission/updatephoto',JSON.stringify(values), {
              headers: {
                // Overwrite Axios's automatically set Content-Type
                'Content-Type': 'application/json',
                'Accept': "application/json"
              }})
              .then((res) => {
                if(res.status===200 ){
                  alert("Photo Updated Successfully");
                }
              })
              .catch(function (error) {
                alert("Error occured:" + error);
              })
              resetForm()
          }, 500);
        }   
    }   
    
      >
      {({values}) => (
        <Form>           
          <Grid item lg={12} xs={12}>
          <div style={{ color: '#283593',borderBottom: '2px solid #283593', borderLeft: '13px solid #283593', padding: '10px', fontSize: '20px', fontWeight: '500', marginBottom: '15px'}}>
          Student Details
         </div>
         <Grid item lg={6} xs={12}> <Field
           component={TextField}
           className={classes.textbox}
           name="formNo"          
           type="text"
           label="Form No"
           variant="outlined"
           fullWidth
          /></Grid>  
          <Grid item lg={6} xs={12}> <Field
           component={TextField}
           className={classes.textbox}
           name="mobileNo"          
           type="text"
           label="Student Registered Mobile NO"
           variant="outlined"
           fullWidth
          /></Grid>
           <Grid item lg={6} xs={6}>
         {fileerror? <Alert severity="error"> Photograph of the Student (max 50Kbs) in JPEG Format only </Alert>:<Alert severity="info">Select PP Size Photograph of the Student (max 50Kbs) in JPEG </Alert>}
        <DropzoneArea
         acceptedFiles={['image/jpeg']}
         maxFileSize={50000}
         filesLimit={1}
         dropzoneText="Student Photograph"
         showPreviewsInDropzone = {true}
        onChange={handleFileChange}
        />
         </Grid>           
      
            <Grid item lg={3} xs={12}>
            <Button
              type='submit'
              variant='contained'
              color='primary'
              fullWidth
              disabled={fileerror}
              style={{height: '2.5rem'}}
             
            >
            Update Photo
            </Button>
          
          </Grid>
      
          </Grid>
        </Form>
       )}
      </Formik>
      </div>
      </Paper>
      </Container>
    </>
  );
};
